@import url(https://fonts.googleapis.com/css2?family=Pirata+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: 'Pirata One', cursive;
  background-color: ivory;
}

.projects main {
  display: grid;
  grid-template-columns: 60px 600px 60px;
}

.main-projects, .contact, .about {
  display: block;
  margin: 0 auto;
  width: 50% ;
  min-width: 750px;
}

nav {
  padding-top: 3px;
  background-color: ivory;
}

nav .border {
  padding: 0 5px;
  border-right: 2px solid grey;
}

nav .last {
  padding-left: 5px;
}

.sticky-footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  text-align: center;
  /* z-index: -1; */
}

aside .buttons {
  height: 100%;
  padding: 5px;
  margin: 5px;
}

.top-button {
  position: fixed;
  top: 47%;
  transform: translateY(-50%);
}

.bottom-button {
  position: fixed;
  top: 50%;
  transform: translateY(-50%);
}

h2 {
  text-align: center;
}

.contact, .about {
  padding: 5px 15px;
}

.contact {
  text-align: center;
}

.go-image {
  display: flex;
}
